@import "../../stylesheet/base.scss";

.container {
  display: none;
  @include tablet {
    display: block;
    width: 230px;
    margin: 40px 0 0 30px;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.17;
    text-transform: uppercase;
    color: #000000;
  }
  @include desktop {
    margin: 0 0 0 30px;
  }
}

.titleBlock {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f6fb;
  border-radius: 20px 20px 0 0;
  width: 230px;
  height: 40px;
  text-align: center;
  letter-spacing: 0.02em;
}

.titleCont {
  display: flex;
  min-width: 110px;
  justify-content: flex-start;
}

.btnBlock {
  display: flex;
  align-items: center;
  width: 30px;
}
.btn {
  border: none;
  background-color: transparent;
}

.list {
  margin-top: 2px;
  min-height: 231px;
  background-color: #f5f6fb;
  border-radius: 0 0 20px 0;
}

.monthPoint {
  display: flex;
  height: 27px;
  line-height: 27px;
  padding: 0 22px;
  justify-content: space-between;
  background-color: #f5f6fb;
  margin-top: 2px;
  &:last-child {
    border-radius: 0 0 20px 0;
  }
}

.monthPointText {
  font-weight: 400;
  color: #52555f;
}
